body {
	color: var(--white);
	padding: 0;
	margin: 0;
	overflow-x: hidden;
	background-color: var(--body);
}

h2,
h3,
h4,
h5,
h6 {
	color: var(--white);
	font-family: 'DM Sans', sans-serif;
	text-transform: capitalize;
}

h1 {
	font-size: 48px;
	font-weight: 600;
	line-height: 120%;
	font-family: 'DM Sans', sans-serif;
}

h2 {
	font-size: 40px;
	line-height: 120%;
	font-weight: 600;
	font-family: 'DM Sans', sans-serif;
	@include breakpoint(max-sm){
		font-size: 29px;
	}
}

h3 {
	font-size: 32px;
	line-height: 120%;
	font-weight: 600;
	font-family: 'DM Sans', sans-serif;
	@include breakpoint(max-sm){
		font-size: 26px;
	}
}

h4 {
	font-size: 24px;
	line-height: 130%;
	font-weight: 600;
	font-family: 'DM Sans', sans-serif;
}

h5 {
	font-size: 20px;
	line-height: 130%;
	font-weight: 600;
	font-family: 'DM Sans', sans-serif;
}

h6 {
	font-size: 16px;
	font-weight: 600;
	line-height: 130%;
	font-family: 'DM Sans', sans-serif;
}
ul {
	padding: 0;
	margin: 0;
	list-style: none;
}
a {
	text-decoration: none;
	text-transform: capitalize;
	margin: 0;
	color: var(--title);
	font-family: 'DM Sans', sans-serif;
	transition: all 0.3s;
}
a:hover {
	text-decoration: none;
	color: var(--title);
}
input:focus{
	color: var(--white);
	outline: none;
}
input{
	color: var(--white);
}
p {
	font-size: 18px;
	line-height: 1.5;
	margin-bottom: 0;
	color: var(--pragraph-color);
	font-family: 'DM Sans', sans-serif;
}

