//nice select scss
.nice-select{
	background: rgba(54, 58, 237, 0.03);
	border-radius: 100px;
	border: 1px solid var(--border);
	color: var(--title);
	font-size: 14px;
	font-weight: 400;
	@include bodyfont;
	width: unset;
	outline: none;
	padding: 13px 24px 13px;
	&:hover{
		border-color: var(--border);
	}
	option{
		color: var(--white);
	}
	.current{
		margin-right: 18px;
	}
}
.nice-select:after{
	right: 23px;
	border-bottom: 1px solid var(--title);
    border-right: 1px solid var(--title);
	width: 10px;
	height: 10px;
}
.cmborder{
	border: 1px solid #D9D9D9;
}
.nice-select.open .list{
	background: var(--white);
	margin-top: 16px;
	width: unset;
}
.nice-select .option.selected.focus{
	background: var(--white);
	outline: none;
}
.nice-select .option{
	border: 1px solid var(--border);
	&:hover{
		background: transparent;
	}
}
