//pagination default
.pagination{
	display: flex;
	align-items: center;
	gap: 12px;
	flex-wrap: wrap;
	a{
		width: 43px;
		height: 43px;
		border-radius: 50%;
		border: 1px solid var(--base);
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 16px;
		font-weight: 600;
		color: var(--base);
		transition: all 0.4s;
		&:hover{
			background: var(--base);
			color: var(--white);
			border-color: var(--base);
			i{
				color: var(--white);
			}
		}
		span{
			margin-top: -4px;
			i{
				font-size: 22px;
				color: var(--text);
			}
		}
	}
	.border-added{
		border: none;
		width: 44px;
		height: 44px;
	}
}
//pagination default

//pagination default
.testimonial__wraptwo .owl-dots{
	.owl-dot{
		span{
			background: var(--border);
			width: 8px;
			height: 8px;
		}
		&.active{
			span{
				width: 28px;
			   background: linear-gradient(90deg, #1A4DBE 0%, #AE5EBE 100%);
		   }
		}
	}
}
