//social meta
.social{
	gap: 12px;
	li{
		a{
			width: 36px;
			height: 36px;
			display: block;
			border-radius: 50%;
			border: 1px solid var(--base);
			display: flex;
			align-items: center;
			justify-content: center;
			i{
				color: var(--white);
				font-size: 16px;
			}
		}
	}
}
.cmn__icon{
	width: 36px;
	height: 36px;
	display: block;
	border-radius: 50%;
	border: 1px solid var(--base);
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 16px;
	color: var(--white);
	i{
		color: var(--white);
		font-size: 16px;
	}
}
