//Section Title//
.section__title{
	.sub{
		position: relative;
        padding-left: 15px;
		display: inline-block;
        @include breakpoint(max-xs){
            font-size: 20px;
        }
		@include breakpoint(max-xxs){
            font-size: 18px;
        }
       &::before{
        position: absolute;
        bottom: 7px;
        left: 0;
        width: 15px;
        height: 2px;
        content: "";
        background: var(--base);
       }
    }
	h2{
		@include breakpoint(max-xl){
			font-size: 38px;
		}
		@include breakpoint(max-md){
			font-size: 36px;
		}
		@include breakpoint(max-sm){
			font-size: 32px;
		}
		@include breakpoint(max-xs){
			font-size: 28px;
		}
		@include breakpoint(max-xxs){
			font-size: 24px;
		}
	}
}
//Section Title//
.container{
	@include breakpoint(xl){
		max-width: 1434px;
		margin: 0 auto;
	}
}

